import Vue from 'vue';
import Component from 'vue-class-component';
import { Action, State } from 'vuex-class';

// import SelectField from 'muse-ui/selectField';
import TextField from 'muse-ui/textField';
import Picker from '~/components/picker/index.vue';
import { IPickerEvent, IPickerItem } from '~/components/picker';
import { MODAL_0 } from '~/config';
import Api from '~/api';
import router from '~/router';

import { IIssueAcquaintanceTask } from '~/entity/taskSelect';
import { ToastMsg } from '~/entity/toast';
import { IPayParams, IAcquaintanceTaskParams } from '~/entity/route_params';

// import Utils from '~/utils';
import { CACHE_NAME } from '~/cache';

import bus from '~/bus/index'; // 引入bus事件存储组建

interface IAcquaintanceTask {
    submitData: IIssueAcquaintanceTask;
    dateOptions: number[];
    isableSubmit: boolean;
    multiLineInputErrorText: string;
}

@Component({
    components: {
        muTextField: TextField,
        picker: Picker
    }
})
export default class AcquaintancesTask extends Vue {
    // 初始化数据
    submitData: IIssueAcquaintanceTask = {
        taskType: 1, // 任务类型{1.定向任务、2.公开任务、3.系统任务}
        subject: '熟人推荐', // 任务标题前缀
        duration: 30, // 任务时长    
        leaveMsg: '', // 留言
        subType: null, // 区分是否需要认证后才能发起的任务类型，2为需要，否则为null    
        totalAmount: 0.00, // 总金额 
        totalCount: null, // 红包总个数
        subsetTasks: [] // 选择的好友列表
    };
    acquaintanceTask: IAcquaintanceTask = {
        submitData: this.submitData,
        dateOptions: [],
        isableSubmit: false,
        multiLineInputErrorText: ''
    };
    leaveMsg = '';

    /* picker 组件 */
    pickerTheme: string = null;
    pickerType: string = null;
    pickerData: IPickerItem[] = null;
    showAcquaintanceTaskModal = false;
    // paramsCache: IAcquaintanceTaskParams = null;

    @State(state => state.acquaintanceState.selectAcquaintanceList)
    selectAcquaintanceList;

    @Action('deleteAcquaintance')
    deleteAcquaintance;

    @Action('resetSelectedAcquaintanceList')
    resetSelectedAcquaintanceList;
    /* picker 组件模态框 */
    @State(state => state.modal.MODAL_0)
    isShowModal;

    @Action('addModal')
    addModal;

    @Action('hideModal')
    hideModal;

    @Action('showToast')
    showToast;

    @Action('showLoading')
    showLoading;
    @Action('hideLoading')
    hideLoading;

    // 利用计算属性监控上传数据是否符合规范
    get isableSubmit() {
        if (this.selectAcquaintanceList.length > 0 && this.leaveMsg.length < 151) {
            return true;
        } else {
            return false;
        }
    }

    get paramsCache() {
        return this['$route'].params;
    }
    
    mounted() {
        console.log('AcquaintancesTask mounted');
        // 退回任务选择时清空数据
        router.beforeEach((to, from, next) => {
            if ((to.name === 'taskSelect' || to.name === 'ai' || to.name === 'personalPage') && from.name === 'acquaintanceTask') {
                this.initData();                
                next();
            } else {
                next();
            }
        });  
        console.log(this.selectAcquaintanceList);
        for (let i = 1; i < 25; i++) {
            this.acquaintanceTask.dateOptions.push(i * 5);
        }        
    }

    activated() {  
        // this.paramsCache = this['$route'].params;
        this.showAcquaintanceTaskModal = true;
        this.calculateAmount();
    }

    deactivated() {
        this.showAcquaintanceTaskModal = false;
        // 退出时关闭模态框
        if (this.isShowModal) {
            this.hideModal();
        }
    }

    initData() {
        this.leaveMsg = '';        
        this.acquaintanceTask.submitData.duration = 30; 
        console.log('initData acquaintanceTask');
    }

    // 删除选中的好友
    deletefriend(fid) {
        this.deleteAcquaintance(fid);
        this.calculateAmount();
    }

    // 留言输入框字数超过提示
    handleMultiLineOverflow(isOverflow) {
        this.acquaintanceTask.multiLineInputErrorText = isOverflow ? '超过字数限制啦！' : '';
        // if (!isOverflow) {
        //     this.selectAcquaintanceList.length > 0 ? this.acquaintanceTask.isableSubmit = true : this.acquaintanceTask.isableSubmit = false;
        // } else {
        //     this.acquaintanceTask.isableSubmit = false;
        // }        
    }

    // 总金额计算方法
    calculateAmount() {
        this.acquaintanceTask.submitData.totalAmount = 0.00;
		// this.selectAcquaintanceList.length > 0 ? this.acquaintanceTask.isableSubmit = true : this.acquaintanceTask.isableSubmit = false;
		for (let i = 0; i < this.selectAcquaintanceList.length; i++) {
			this.acquaintanceTask.submitData.totalAmount += this.selectAcquaintanceList[i].amount;
		}
    }
    // 显示日期选择    
    showDuetimeSet() {
        const defaultDuetime = this.acquaintanceTask.submitData.duration || 30;
        this.pickerData = [
            {
                list: this.acquaintanceTask.dateOptions,
                currentIndex: this.acquaintanceTask.dateOptions.indexOf(this.acquaintanceTask.submitData.duration)
            }
        ];
        this.pickerTheme = 'dueTimeSet';
        this.pickerType = '';
        this.addModal(MODAL_0);
    }
    cancel() {
        if (this.isShowModal) {
            // history.back();
            this.hideModal();
        }
    }

    confirm(event: IPickerEvent) {
        this.acquaintanceTask.submitData.duration = event.values[0];
        this.cancel();
    }

    async submitOrder() {
        const totalCount = this.selectAcquaintanceList.length;        
        if (totalCount > 0) {
            this.acquaintanceTask.submitData.leaveMsg = this.leaveMsg;
            this.acquaintanceTask.submitData.subsetTasks = [].concat(this.selectAcquaintanceList);
            this.acquaintanceTask.submitData.totalCount = this.acquaintanceTask.submitData.subsetTasks.length;
            this.acquaintanceTask.submitData.subType = this.$route.params.action === 'fromFindMatchMaker' ? 2 : null;
            this.showLoading();
            try {  
                const taskRes = await Api.submitTaskOrder(this.acquaintanceTask.submitData); 
                this.hideLoading();                        
                const taskInfo = taskRes.body.obj;
                // 复原数据
                this.initData();  
                this.resetSelectedAcquaintanceList([]); 
                const params: IPayParams = {
                    taskId: taskInfo.taskId,
                    action: 'fromTask'
                };
                
                // Utils.saveItem(CACHE_NAME.payParams, params);
                bus.$emit(CACHE_NAME.payParams, params);

                this['$router'].replace({
                    name: 'pay',
                    params
                });
            } catch (e) {
                this.hideLoading();
                this.showToast(new ToastMsg(e.message));                
            }   
        } else {
            // this.acquaintanceTask.isableSubmit = false;
            this.showToast(new ToastMsg('请至少选择一个好友'));
        }
    }
}



